import { makeObservable, observable, action } from 'mobx';
import { PMDataVM } from '../../../single/form/PMDataVM';
import * as pvt from '../../project/ProjectViewType'

export const pmDataVM = () => PMDataVM.Instance

export class LeftNavVM {
  constructor() {
    makeObservable(this, {
      projectViewType : observable
    })
  }

  get Title (){
    return pmDataVM().CurrentProject?.Name
  }

  get Items (){
    return this.ModelItem.Items
  }

  projectViewType = undefined
  get ProjectViewType(){
    if (!this.projectViewType){
      this.projectViewType = pmDataVM().ProjectViewType;
    }
    return this.projectViewType
  } set ProjectViewType(v){
    this.projectViewType = pmDataVM().ProjectViewType = v;
  }

  GetItemClass = (item) => {
    return `leftnav__item ${item.Value === this.ProjectViewType ? 'active' : ''}`;
  }

  SetView = (item) => {
    this.ProjectViewType = item.Value;
  }
}