import { LeftNav } from "./LeftNav"
import * as pvt from '../../project/ProjectViewType'
import * as itemUtil from './LeftNavItemUtil'

export const GetTest = () => {
  var qI = new LeftNav();

  qI.Title = 'HikariComplex'
  qI.Items = [
    itemUtil.get('MASS', pvt.mass),
    itemUtil.get('TYPICAL LEVEL', pvt.typicallevel),
    itemUtil.get('PHASE', pvt.phase),
    itemUtil.get('INPUT DATA', pvt.inputdata),
  ]

  return qI;
}