import React, { Component } from 'react';
import { observer } from 'mobx-react';
import { } from '@ant-design/icons';
import './LeftNav.css'
import { LeftNavVM } from './LeftNavVM';
import * as navUtil from './LeftNavVMUtil'

class LeftNavView extends Component {
  constructor(props) {
    super(props);

    var { store } = this.props;
    if (!store) {
      this.state = navUtil.GetTest();
    } else {
      this.state = store;
    }
  }

  render() {
    var data = this.state;

    return (
      <>
        <nav className='leftnav'>
          {/* Wrapper */}
          <div className='leftnav__wrapper'>
            {/* Title */}
            <span className='leftnav__title'>
              <span className='leftnav__title__prefix'>
                D N
              </span>
              <span className='leftnav__title__name'>
                {data.Title}
              </span>
            </span>

            {/* Items */}
            <div className='leftnav__items'>
              {data.Items.map(x => {
                return <a key={x.Value} onClick={() => data.SetView(x)}
                  className={data.GetItemClass(x)}>
                  {x.Title}
                </a>
              })}
            </div>
          </div>
        </nav>
      </>
    );
  }
}

export default observer(LeftNavView);
