import React, { Component } from 'react';
import { observer } from 'mobx-react';
import { Col, Row } from 'antd';
import { withRouter } from "react-router-dom";
import ProjectCompVM from './ProjectCompVM';
import Phase_DictView from '../../phase/Phase_DictView';
import MassResultCompView from '../../mass/result/MassResultCompView';
import RebarMass_DictView from '../../mass/rebar/RebarMass_DictView';
import Level_DictView from '../../level/Level_DictView';
import TypicalLevelSettingView from '../../setting/typicallevel/TypicalLevelSettingView';
import LeftNavView from '../../nav/left/LeftNavView';
import './ProjectComp.css'
import * as util from './ProjectCompVMUtil'

class ProjectCompView extends Component {
  constructor(props) {
    super(props);

    var { store } = this.props;
    if (!store) {
      this.state = util.get()
    } else {
      this.state = store;
    }
  }

  componentDidMount() {
    var data = this.state;
    const id = this.props.match.params.id;
    data.id = id;
  }

  render() {
    var data = this.state;
    var projectVM = data.ProjectVM;

    return (
      <>
        <div className='projectcomp'>
          <LeftNavView />
          <div className='projectcomp__main__wrapper'>
            <div className='projectcomp__main'>
              {projectVM ? data.ViewElement : ''}
            </div>
            <div className='projectcomp__main__bottom'/>
          </div>
        </div>
      </>
    );
  }
}

export default withRouter(observer(ProjectCompView));
